body {
  background-color: #073332;
  margin: 0;
  padding: 0;
  font-family: 'Times New Roman', Times, serif;
  position: relative;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#root {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}
